import React, { Component } from 'react';
import { Breadcrumb, BreadcrumbItem, Button, Form,
    FormGroup, Label, Input, Col, FormFeedback } from 'reactstrap';
import { Link } from 'react-router-dom';

//controlled forms can only be made using class components
class Contact extends Component {

    constructor(props){
        super(props);
        
        //initiating the state of the form elements
        this.state = {
            firstname: "",
            lastname: "",
            telnum: "",
            email: "",
            agree: false,
            contactType: "Tel.",
            message: "",
            touched: {
                firstname: false,
                lastname: false,
                telnum: false,
                email: false 
            }
        }
        //making the properties available to the handleSubmit method.
        this.handleSubmit = this.handleSubmit.bind(this);
        this.handleInputChange = this.handleInputChange.bind(this);
        this.handleBlur = this.handleBlur.bind(this);
    }

    //this method is invoked with ANY change in the state
    //or the input boxes.
    handleInputChange(event){
        // target carries any changes that have been made in
        // the event.
        const target = event.target;
        // if the target type is checkbox then the value is
        // retreived from the checked property or the 
        // target.value.
        const value = target.type === 'checkbox' ? target.checked : target.value;
        //stores the name value, can be firstname, lastname,
        // email,etc.
        const name = target.name;
        
        //changing the state of the particular value that has
        // been changed.
        this.setState({
            [name]: value
        });
    }

    handleSubmit(event){
        //JSON>stringify() method is used to convert the data
        // into string so that it can be converted into JSON object
        // and the data can be stored into the database or 
        // can be shared as an api.
        console.log("Current State is: " + JSON.stringify(this.state));

        //alerting the user that the form has been submitted
        alert("Current State is: " + JSON.stringify(this.state));

        //prevent from going to another page.
        event.preventDefault();
    }

    //this function indicates which particular field has
    // been modified, this is a DOUBLE-ARROW/CURRIED function.
    // as the function receives two parameters.
    // ... is known as the "spread operator" and can be used for
    // different operations.
    // if any field in the form is edited, the state will be
    // set to "true" instead of "false" in touched.
    handleBlur = (field) => (evt) => {
        this.setState({
            touched: {...this.state.touched, [field]: true}
        })
    }

    //validating the form each time the form is rendered
    // initially the value will be empty , but if there is
    // an error, the value will be set to that error message.
    validate(firstname, lastname, telnum, email){
        const errors = {
            firstname: "",
            lastname: "",
            telnum: "",
            email: ""
        };

        if(this.state.touched.firstname && firstname.length < 3){
            errors.firstname = 'First Name should be >= 3 characters!';
        }
        else if(this.state.touched.firstname && firstname.length > 10){
            errors.firstname = 'First Name should be <= 10 characters!';
        }

        if(this.state.touched.lastname && lastname.length < 3){
            errors.lastname = 'Last Name should be >= 3 characters!';
        }
        else if(this.state.touched.lastname && lastname.length > 10){
            errors.lastname = 'Last Name should be <= 10 characters!'
        }
        
        //reglar expression for the mobile number
        const reg = /^\d+$/;
        if (this.state.touched.telnum && !reg.test(telnum)){
            errors.telnum = 'Tel. Number should only contain numbers!'
        }
        
        //checking if email has @ or not
        if (this.state.touched.email && email.split('').filter(x => x === '@').length !== 1){
            errors.email = 'Email should contain a @'
        }

        return errors;
    }


    render(){
        //invoking the validate function.
        const errors = this.validate(this.state.lastname, this.state.firstname, this.state.telnum, this.state.email)
        
        return(
            <div className="container">
                <div className='row'>
                    <Breadcrumb>
                        <BreadcrumbItem><Link to='/home'>Home</Link></BreadcrumbItem>
                        <BreadcrumbItem active>Contact Us</BreadcrumbItem>
                    </Breadcrumb>
                    <div className='col-12'>
                        <h3>Contact Us</h3>
                        <hr />
                    </div>
                </div>
                <div className="row row-content">
                    <div className="col-12">
                    <h3>Location Information</h3>
                    </div>
                    <div className="col-12 col-sm-4 offset-sm-1">
                            <h5>Our Address</h5>
                            <address>
                            121, Clear Water Bay Road<br />
                            Clear Water Bay, Kowloon<br />
                            HONG KONG<br />
                            <i className="fa fa-phone"></i>: +852 1234 5678<br />
                            <i className="fa fa-fax"></i>: +852 8765 4321<br />
                            <i className="fa fa-envelope"></i>: <a href="mailto:confusion@food.net">confusion@food.net</a>
                            </address>
                    </div>
                    <div className="col-12 col-sm-6 offset-sm-1">
                        <h5>Map of our Location</h5>
                    </div>
                    <div className="col-12 col-sm-11 offset-sm-1">
                        <div className="btn-group" role="group">
                            <a role="button" className="btn btn-primary" href="tel:+85212345678"><i className="fa fa-phone"></i> Call</a>
                            <a role="button" className="btn btn-info"><i className="fa fa-skype"></i> Skype</a>
                            <a role="button" className="btn btn-success" href="mailto:confusion@food.net"><i className="fa fa-envelope-o"></i> Email</a>
                        </div>
                    </div>
                </div>

                <div className="row row-content">
                   <div className="col-12">
                      <h3>Send us your Feedback</h3>
                   </div>
                    <div className="col-12 col-md-9">
                        <Form onSubmit={this.handleSubmit}>
                            <FormGroup row>
                                <Label htmlFor="firstname" md={2}>First Name</Label>
                                <Col md={10}>
                                    <Input type="text" id="firstname" name="firstname"
                                        placeholder="First Name"
                                        value={this.state.firstname}
                                        valid={errors.firstname === ""}
                                        invalid={errors.firstname !== ""}
                                        onBlur={this.handleBlur('firstname')}
                                        onChange={this.handleInputChange} />
                                    
                                    <FormFeedback>{errors.firstname}</FormFeedback>

                                </Col>
                            </FormGroup>
                            <FormGroup row>
                                <Label htmlFor="lastname" md={2}>Last Name</Label>
                                <Col md={10}>
                                    <Input type="text" id="lastname" name="lastname"
                                        placeholder="Last Name"
                                        value={this.state.lastname}
                                        valid={errors.lastname === ""}
                                        invalid={errors.lastname !== ""}
                                        onBlur={this.handleBlur('lastname')}
                                        onChange={this.handleInputChange} />

                                    <FormFeedback>{errors.lastname}</FormFeedback>

                                </Col>                        
                            </FormGroup>
                            <FormGroup row>
                            <Label htmlFor="telnum" md={2}>Contact Tel.</Label>
                                <Col md={10}>
                                    <Input type="tel" id="telnum" name="telnum"
                                        placeholder="Tel. number"
                                        value={this.state.telnum}
                                        valid={errors.telnum === ""}
                                        invalid={errors.telnum !== ""}
                                        onBlur={this.handleBlur('telnum')}
                                        onChange={this.handleInputChange} />

                                    <FormFeedback>{errors.telnum}</FormFeedback>

                                </Col>
                            </FormGroup>
                            <FormGroup row>
                                <Label htmlFor="email" md={2}>Email</Label>
                                <Col md={10}>
                                    <Input type="email" id="email" name="email"
                                        placeholder="Email"
                                        value={this.state.email}
                                        valid={errors.email === ""}
                                        invalid={errors.email !== ""}
                                        onBlur={this.handleBlur('email')}
                                        onChange={this.handleInputChange} />

                                    <FormFeedback>{errors.email}</FormFeedback>

                                </Col>
                            </FormGroup>
                            <FormGroup row>
                                <Col md={{size: 6, offset: 2}}>
                                    <FormGroup check>
                                        <Label check>
                                            <Input type="checkbox"
                                                name="agree"
                                                checked={this.state.agree}
                                                onChange={this.handleInputChange} /> {" "}
                                            <strong>May we contact you?</strong>
                                        </Label>
                                    </FormGroup>
                                </Col>
                                <Col md={{size: 3, offset: 1}}>
                                    <Input type="select" name="contactType"
                                            value={this.state.contactType}
                                            onChange={this.handleInputChange}>
                                        <option>Tel.</option>
                                        <option>Email</option>
                                    </Input>
                                </Col>
                            </FormGroup>
                            <FormGroup row>
                                <Label htmlFor="message" md={2}>Your Feedback</Label>
                                <Col md={10}>
                                    <Input type="textarea" id="message" name="message"
                                        rows="12"
                                        value={this.state.message}
                                        onChange={this.handleInputChange}></Input>
                                </Col>
                            </FormGroup>
                            <FormGroup row>
                                <Col md={{size: 10, offset: 2}}>
                                    <Button type="submit" color="primary">
                                        Send Feedback
                                    </Button>
                                </Col>
                            </FormGroup>
                        </Form>
                    </div>
               </div>
            </div>
        );
    }
}


export default Contact;